@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Serif:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&display=swap");
html {
  box-sizing: border-box;
  font-size: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

.total-wrapper {
  padding-bottom: 20px;
}

body {
  height: 100vh;
  margin: 0;
  padding: 0;
  line-height: 1.3;
  font-family: "IBM Plex Serif", serif;
  background-color: #E5E5E5;
}

ul {
  margin: 0;
  padding: 0;
}

h1, h2, h3 {
  font-family: "IBM Plex Serif", serif;
  margin-top: 0;
  line-height: 1.1;
  margin-bottom: 0.5em;
}

.lhs {
  width: 50%;
  display: inline-block;
}
.lhs:before {
  content: "";
  position: absolute;
  height: 245vh;
  border: 1px solid #000000;
  right: 50%;
}

.rhs {
  display: inline-block;
  vertical-align: top;
  width: 48%;
  margin-left: 20px;
}

.profile-pic img {
  border-radius: 20px;
  background-color: #000000;
}

nav .navbar-brand.title {
  background: #f5c1a7;
  color: #000;
  font-family: Libre Baskerville;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  height: 24px;
  line-height: 17px;
  margin: 0;
  padding: 12px 0 26px 20px;
  width: 170px;
  text-align: left;
  display: inline-block;
}
nav img {
  width: 40px;
  height: auto;
}

.backto {
  border-bottom: 1px solid #000;
  color: #000;
  font-family: IBM Plex Sans;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 225%;
  padding-bottom: 4px;
  text-decoration: none;
  margin-left: 15px;
}

.AddCollabarator {
  border: none;
  color: #000;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  letter-spacing: 0.015em;
  display: inline-block;
  width: 150px;
  margin: 10px 0 0 460px;
  border-radius: 15px;
  background-color: #E46125;
  padding: 6px;
}
.AddCollabarator.publish {
  margin: 0;
}

.add-btn {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  letter-spacing: 0.015em;
  color: #E46125;
  background-color: transparent;
  display: inline-block;
  width: 11%;
  border: 1px solid #e46125;
  border-radius: 20px;
  margin-right: 10px;
}
.add-btn.preview {
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  padding: 7px 0;
}

#Collabarator .collabarator-title h4 {
  font-family: "IBM Plex Serif";
  font-style: normal;
  font-weight: 700;
  font-size: 36px;
  line-height: 47px;
  color: #000000;
}
#Collabarator .collabarator-title .collabarator-tags li {
  border: 1px solid #000;
  border-radius: 20px;
  cursor: pointer;
  display: inline-block;
  margin-right: 13px;
  padding: 5px 20px;
  text-transform: lowercase;
  background-color: #fff;
  cursor: pointer;
}
#Collabarator .collabarator-title .collabarator-tags li a {
  font-family: "IBM Plex Sans";
  font-style: normal;
  font-weight: 300;
  font-size: 12px;
  line-height: 16px;
}
#Collabarator .sample-text {
  font-family: "IBM Plex Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #000;
  margin: 20px 0;
}
#Collabarator .hr {
  width: 96%;
  border: 1px solid black;
}

#Question-Pallete {
  margin-bottom: 20px;
}
#Question-Pallete .que-title h6 {
  font-family: "IBM Plex Serif";
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 31px;
  color: #000000;
  margin-bottom: 20px;
}
#Question-Pallete textarea {
  border: none;
  border-radius: 9px;
}
#Question-Pallete.answer-pallete .que-title {
  display: inline-block;
}
#Question-Pallete.answer-pallete .add-user-icon {
  display: inline-block;
  width: 80%;
  text-align: right;
  cursor: pointer;
}
#Question-Pallete.reference .que-title {
  display: block;
}
#Question-Pallete.reference .reference-text {
  display: inline-block;
  width: 85%;
}
#Question-Pallete .input-text {
  color: #ababab;
  font-family: "IBM Plex Sans";
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  border: none;
  width: 95%;
  padding: 10px;
  border-radius: 10px;
}

.saveandproceed {
  color: #fff;
  background: #000000;
  border: 1px solid #FFFFFF;
  box-sizing: border-box;
  box-shadow: -4px -4px 8px 1px #ffffff, 4px 4px 8px 1px rgba(134, 170, 253, 0.33);
  border-radius: 49.5px;
  padding: 7px 14px 14px 14px;
  float: right;
  margin-right: 20px;
}

.btn-grp {
  text-align: right;
}

.accordion-item {
  margin-bottom: 20px;
}

.accordion-button {
  background: #F5F5F5;
  border: 2px solid #EAEAEA;
  box-sizing: border-box;
  box-shadow: -4px -4px 8px 1px rgba(255, 255, 255, 0.86), 4px 4px 15px 5px rgba(111, 113, 119, 0.09);
  border-radius: 9px;
}

#EditIcons .user-icons {
  text-align: right;
}
#EditIcons .user-icons img {
  margin-right: 10px;
  margin-bottom: 10px;
}

.card {
  border-radius: 5px;
  height: auto;
  overflow: hidden;
  padding: 15px;
  width: 570px;
  margin-bottom: 20px;
}
.card .top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  font-family: "IBM Plex Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #ABABAB;
}
.card .top button {
  background: #5256ad;
  color: #ffffff;
  padding: 3px 13px;
  border: 0;
  cursor: pointer;
}

.form-wrapper {
  height: 160px;
  width: 100%;
  background-color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
}
.form-wrapper .select {
  color: #5256ad;
  cursor: pointer;
  margin-left: 7px;
  background-color: #E3E3E3;
  border-radius: 22px;
  padding: 10px;
}
.form-wrapper .select img {
  width: 22px;
  padding-bottom: 6px;
}
.form-wrapper input {
  display: none;
}
.form-wrapper.dragover {
  border-style: solid;
  font-size: 2rem;
  color: #c8c9dd;
  font-weight: 600;
  background: rgba(0, 0, 0, 0.34);
}

.content {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%;
  overflow-y: auto;
  max-height: 300px;
  margin-top: 20px;
  margin-left: 20px;
  position: relative;
}
.content .slider-inner .image {
  display: none;
  height: 85px;
  width: 85px;
  border-radius: 5px;
  overflow: hidden;
  position: relative;
  margin-bottom: 7px;
  margin-right: 7px;
}
.content .slider-inner .image:nth-child(4n) {
  margin-right: 0;
}
.content .slider-inner .image.active {
  display: inline-block;
}
.content .slider-inner .image img {
  width: 100%;
  height: 100%;
}
.content .slider-inner .image span {
  position: absolute;
  top: -4px;
  right: 5px;
  cursor: pointer;
  font-size: 22px;
  color: #fff;
}
.content .slider-inner .image span:hover {
  opacity: 0.8;
}

.slider-outer {
  position: relative;
}
.slider-outer .prev, .slider-outer .next {
  float: left;
  cursor: pointer;
  position: relative;
  z-index: 100;
}
.slider-outer .prev {
  background-color: #000000;
  margin-right: -45px;
  position: absolute;
  bottom: 60px;
  padding: 10px;
  border-radius: 20px;
}
.slider-outer .next {
  background-color: #000000;
  bottom: 70px;
  left: 106%;
  margin-left: inherit;
  position: absolute;
  padding: 10px;
  border-radius: 20px;
}

.EasyMDEContainer .CodeMirror {
  width: 590px;
}